}

.wrapper {
  background-image: url(/Weather/static/media/3.1a6ea0b6.jpg);
  background-image-size: 100%;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2db0e0', endColorstr='#6028a8', GradientType=1 );
}

.main {
  
  background-blend-mode: multiply;
  height: 400px;
  background: #2a6bb1;
  box-shadow: 0px 2px 80px -20px rgba(0,0,0,0.5);
  width: 60%;
  margin: 0 auto;
  opacity: .9;
  }

.info {
  height: 100%;
  background-blend-mode: multiply;
  background-color: #000;
  border: 2px solid #1f1f1f;
  border-right: 2px solid #131e29;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;

}
.wethinf{
  background-color: #000;
  
}

.form {
  
  padding-top: 250px;
  padding-left: 150px;
  border-top: 2px solid #113a65;
  border-bottom: 2px solid #113a65;
  border-right: 2px solid #113a65;
}

input[type="text"] {
text-align: left;
  background-color: transparent;
  border: 0;
  border-bottom: solid 2px #fff;
  height: 60%;
  padding-bottom: 4px;
  color: #fff;
  font-weight: lighter;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
  outline: none;

background-color: #000;
}

input::-webkit-input-placeholder {
  color: #fff;
}

input::-moz-placeholder {
  color: #fff;
}

button {
  border: 2px solid #fff;
  border-bottom: 4px solid #fff;
  
  width: 30%;
  margin: 0 2px;
  border-radius: 2px;
  cursor: pointer;
  background-color: #000;
  color: #fff;
  outline: none!important;
}

button:hover {
  border-bottom: 2px solid #103a65;
  position: relative;
  top: 2px;
}

button:active {
  border-bottom: 2px solid transparent;
  border-color: transparent;
  position: relative;
  top: 2px;
}

.info h2 {
  font-size: 3em;
  color: #fff;
}

.info p {
  color: #e4e4e4;
  font-weight: bold;
}

.infoWeath {
  width: 60%;
  font-size: 20px;
  color: #fff;

}

.error {
  color: #041f3c;
  font-weight: bold;
}
